import { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import ProductCard from "../../../components/ProductCard";
// @ts-ignore

export default function ProductDetail() {
  const { id } = useParams();
  const [product, setProduct] = useState<any>(null);
  const [error, setError] = useState<string>("");
  const [isFavorite, setIsFavorite] = useState(false);

  useEffect(() => {
    fetch(`https://fakestoreapi.com/products/${id}`)
      .then((res) => {
        if (!res.ok) throw new Error("Product not found");
        return res.json();
      })
      .then((data) => setProduct(data))
      .catch((err) => setError(err.message));
  }, [id]);

  useEffect(() => {
    if (product) {
      const favs = localStorage.getItem("favorites");
      const favArr = favs ? JSON.parse(favs) : [];
      setIsFavorite(favArr.some((item: any) => item.id === product.id));
    }
  }, [product]);

  const toggleFavorite = () => {
    const favs = localStorage.getItem("favorites");
    let favArr = favs ? JSON.parse(favs) : [];
    if (isFavorite) {
      favArr = favArr.filter((item: any) => item.id !== product.id);
    } else {
      favArr.push(product);
    }
    localStorage.setItem("favorites", JSON.stringify(favArr));
    setIsFavorite(!isFavorite);
  };

  if (!product) return <div>Loading...</div>;
  if (error)
    return (
      <div className="text-center text-red-500 text-xl py-10">{error}</div>
    );

  return (
    <div style={{ maxWidth: 600, margin: "auto" }}>
      <img
        src={product.image}
        alt={product.title}
        style={{ width: "100%", maxHeight: 400, objectFit: "contain" }}
      />
      <h2>{product.title}</h2>
      <p>{product.description}</p>
      <h3>${product.price}</h3>
      <button
        onClick={toggleFavorite}
        style={{ background: isFavorite ? "gold" : "gray" }}
      >
        {isFavorite ? "Unfavorite" : "Favorite"}
      </button>
    </div>
  );
}
